.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  text-align: center;
  background: #fff;
  padding: 20px;
}

#submitterbit {
  position: fixed;
  background-color: #fff;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  margin: 0;
  padding: 5px;
  border-top: solid 2px #444;
}


input {
  width: 280px;
}

input:read-only {
  border: 0;
  color: #545458;
  font-weight: bold;
  text-align: center;
}

button {
  width: 100%;
  max-width: 300px;
  appearance: button;
  padding: 13px 16px;
  background-color: #AEAEAE;
  box-shadow: inset 0 -4px 0 #110221;
  border-radius: 10px;
  color: #FFFFFF;
  cursor: pointer;
  display: inline-block;
  font-family: sans-serif;
  font-size: 15px;
  font-weight: 700;
  letter-spacing: 3px;
  line-height: 20px;
  margin: 2px;
  border: solid transparent;
  border-width: 0;
  text-transform: uppercase;
  touch-action: manipulation;
   transition-duration: 0.05s;
  transition-timing-function: linear;
  transition-delay: 0;
  user-select: none;
  -webkit-user-select: none;
}

.care {
  background-color: #d03fc2;
}

.timemoney {
  background-color: #F4C630;
}

.health {
  background-color: #4783F5;
}

button.active {
  padding-top: 17px;
  padding-bottom: 9px;
  box-shadow: inset 0 4px 0 #fff;
}


button:hover:not(:disabled) {
  filter: brightness(1.1);
  -webkit-filter: brightness(1.1);
}

#submitbutton {
  background-color: #444448;
  color: #f8f8ff;
}
#successbutton {
  background-color: #30f550;
  color: forestgreen;
}

.gyear {
  display: inline-block;
  cursor: default;
}

.gweek {
  display: block;
  padding: 0;
  height: 25px;
  line-height: 25px;
  font-size: x-small;
  color: #444;
}

.gweekLeftgutter, .gweekRightgutter {
  padding-left: 5px;
  padding-right: 5px;
  display: inline-block;
  width: 80px;
  text-align: right;
}

.gday {
  display: inline-block;
  width: 24px;
  height: 24px;
  margin: 0;
  border: 1px solid #ccd4e2;
  border-right: 0;
  border-bottom: 0;
  vertical-align: top;
  font-size: xx-small;
  color: #aaaab4;
}

.gday:nth-last-child(2) {
  border-right: 1px solid #ccd4e2;
}

.gweek:last-child > .gday {
  border-bottom: 1px solid #ccd4e2;
}

.gday:nth-last-child(2), .gday:nth-last-child(3) {
  background-color: #fafafb;
}

.gday:hover {
  color: #000000;
  background-color: #e9e9e9;
}